import { Button } from "@material-ui/core";
import React from "react";

const LangButton = ({ langButtonValue, sendDataToParent }) => {
  return (
    <Button
      variant="contained"
      onClick={() => {
        sendDataToParent(langButtonValue);
      }}
    >
      {langButtonValue.name}
    </Button>
  );
};

export default LangButton;
